.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background-color: white;
  z-index: 1;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  margin: 8px;
  max-width: 270px;
  max-height: 400px;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product-info {
  display: flex;
  height: 100px;
  margin-bottom: 15px;
}

.product_rating {
  display: flex;
}

.price {
  color: rgb(136, 14, 14);
  font-weight: bold;
  margin-top: 2px;
}

.product > button {
  background-color: #f0c14b;
  border: 1px solid;
  margin-top: 8px;
  padding: 5px;
  border-color: #a88734;
  color: black;
  border-radius: 5px;
}
